import React from 'react';
import InputElement from '../../../Atoms/InputElement';
import './add-flight.scss';
import Button from '../../../Atoms/Button';
import { Dropdown as DropdownComponent } from '../../../Atoms/DropdownComponent';
import Date_picker from '../../../Atoms/Datepicker';
import { AircraftRegActions } from '../../../../Store/AircraftReg/action';
import { connect } from 'react-redux';
import { StationActions } from '../../../../Store/Station/action';
import { flightUpdateActions } from '../../../../Store/flightManagement/flightUpdate/flightUpdate.action';
import { commonAction } from '../../../../Store/commonActivity/action';
import { timeValidator, tzFormat } from '../calculation';
import moment from 'moment';
import { templateActions } from '../../../../Store/flightManagement/template/template.action';
export class AddFlightList extends React.Component {
  obj = {
    aircraftregistrationno: '',
    airlineaircrafttype: '',
    iataaircrafttype: '',
    fltnumber: '',
    depgateno: '',
    schdeputcdttm: '',
    scharrutcdttm: '',
    estdeputcdttm: '',
    estarrutcdttm: '',
    actdeputcdttm: '',
    onutcdttm: '',
    offutcdttm: '',
    actarrutcdttm: '',
    fltdt:
      moment(new Date())
        .toISOString()
        .split('.')[0] + 'Z',
    arrtermno:
      moment(new Date())
        .toISOString()
        .split('.')[0] + 'Z',
    depstncd: '',
    arrstncd: '',
    fltstatus: 'Schedulled',
  };

  constructor(props) {
    super(props);
    this.state = {
      submitted: false,
    };
    this.departureDate = new Date();
    this.arrivalDate = new Date();
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  setTimeWarning = () =>
    commonAction.openAlert({
      title: 'Warning',
      content: 'Format should be 00:00',
    });

  checkValid = value => {
    if (!timeValidator(this.obj[value])) {
      this.obj[value] = '';
      this.setTimeWarning();
      this.setState({});
    }
  };

  handleSubmit(e) {
    e.preventDefault();
    this.setState({ submitted: true });
    this.props.postAddCall(this.obj);
  }

  componentDidMount() {
    this.props.aircraftRegdd();
    this.props.stationdd();
    this.props.fltstatusDD();
  }

  render() {
    let AirRegDDdata = [];
    let AirTypeDDdata = [];
    let Stationdddata = [];

    console.log('obj', this.obj);
    this.props.aircraftRegddreducer.map((item, index) => {
      AirTypeDDdata.push({ title: item.aircrafttype, selected: false });
      AirRegDDdata.push({ title: item.aircraftregno, selected: false });
    });
    this.props.stationreducer.map((item, index) =>
      Stationdddata.push({ title: item.station, selected: false })
    );
    console.log('listtt', this.props);

    return (
      <>
        <div className="add-flight">
          <div className="af-details">
            <div className="af-div pad-12r">
              <div className="af-title"> Aircraft Type </div>
              <div>
                <DropdownComponent
                  className="dd_box"
                  placeholder={`Select`}
                  list={AirTypeDDdata}
                  toggleItem={val => {
                    this.obj.airlineaircrafttype = val.title;
                    this.obj.iataaircrafttype = val.title;
                  }}
                />
              </div>
            </div>
            <div className="af-div pad-12r">
              <div className="af-title"> AC Registration</div>
              <div>
                <DropdownComponent
                  className="dd_box"
                  placeholder={`Select`}
                  list={AirRegDDdata}
                  toggleItem={val => {
                    this.obj.aircraftregistrationno = val.title;
                  }}
                />
              </div>
            </div>
            <div className="af-div ">
              <div className="af-title">Flight Number</div>
              <div>
                <InputElement
                  className="Add_flight_box"
                  placeholder={`Enter`}
                  onChange={e => {
                    this.obj.fltnumber = e.target.value;
                  }}
                />
              </div>
            </div>
          </div>
          <div className="af-details pad-7t">
            <div className="af-div pad-12r">
              <div className="af-title-dod">Date Of Departure</div>
              <div>
                <Date_picker
                  className="Add_flight_box"
                  Getbackdate={date => {
                    this.departureDate = date;
                    this.obj.fltdt =
                      moment(date)
                        .toISOString()
                        .split('.')[0] + 'Z';
                  }}
                />
              </div>
            </div>
            <div className="af-div ">
              <div className="af-title-doa">Date Of Arrival</div>
              <div>
                <Date_picker
                  className="Add_flight_box"
                  Getbackdate={date => {
                    this.arrivalDate = date;
                    this.obj.arrtermno =
                      moment(date)
                        .toISOString()
                        .split('.')[0] + 'Z';
                  }}
                />
              </div>
            </div>
          </div>

          <div className="af-details pad-7t">
            <div className="af-div pad-12r">
              <div className="af-title"> Dep Station</div>
              <DropdownComponent
                className="dd_box"
                title="Select"
                list={Stationdddata}
                placeholder="Select"
                toggleItem={val => {
                  this.obj.depstncd = val.title;
                }}
              />
            </div>

            <div className="af-div pad-12r1">
              <div className="af-title"> Arrival Station</div>
              <DropdownComponent
                className="dd_box"
                list={Stationdddata}
                placeholder={`Select`}
                toggleItem={val => {
                  this.obj.arrstncd = val.title;
                }}
              />
            </div>

            <div className="af-div pad-12r2">
              <div className="af-title"> Departure Terminal</div>
              <InputElement
                className="Add_flight_box"
                onChange={e => {
                  this.obj.depgateno = e.target.value;
                }}
                placeholder={`Enter`}
              />
            </div>
          </div>

          <div className="af-details">
            <div className="af-div pad-12r">
              <div className="af-title">STD</div>
              <InputElement
                className="Add_flight_box"
                value={this.obj.schdeputcdttm}
                onChange={e => {
                  this.obj.schdeputcdttm = e.target.value;
                  this.setState({});
                }}
                blur={() => this.checkValid('schdeputcdttm')}
                placeholder={`Enter`}
              />
            </div>

            <div className="af-div pad-12r">
              <div className="af-title">STA</div>
              <InputElement
                className="Add_flight_box"
                value={this.obj.scharrutcdttm}
                onChange={e => {
                  this.obj.scharrutcdttm = e.target.value;
                  this.setState({});
                }}
                blur={() => this.checkValid('scharrutcdttm')}
                placeholder={`Enter`}
              />
            </div>
          </div>

          <div className="af-details">
            <div className="af-div pad-12r">
              <div className="af-title">ETD</div>
              <InputElement
                className="Add_flight_box"
                value={this.obj.estdeputcdttm}
                onChange={e => {
                  this.obj.estdeputcdttm = e.target.value;
                  this.setState({});
                }}
                blur={() => this.checkValid('estdeputcdttm')}
                placeholder={`Enter`}
              />
            </div>

            <div className="af-div pad-12r">
              <div className="af-title">ETA</div>
              <InputElement
                className="Add_flight_box"
                value={this.obj.estarrutcdttm}
                onChange={e => {
                  this.obj.estarrutcdttm = e.target.value;
                  this.setState({});
                }}
                blur={() => this.checkValid('estarrutcdttm')}
                placeholder={`Enter`}
              />
            </div>
          </div>

          <div className="af-details">
            <div className="af-div pad-12r">
              <div className="af-title">ATD</div>
              <InputElement
                className="Add_flight_box"
                value={this.obj.actdeputcdttm}
                onChange={e => {
                  this.obj.actdeputcdttm = e.target.value;
                  this.setState({});
                }}
                blur={() => this.checkValid('actdeputcdttm')}
                placeholder={`Enter`}
              />
            </div>

            <div className="af-div pad-12r">
              <div className="af-title">ON</div>
              <InputElement
                className="Add_flight_box"
                value={this.obj.onutcdttm}
                onChange={e => {
                  this.obj.onutcdttm = e.target.value;
                  this.setState({});
                }}
                blur={() => this.checkValid('onutcdttm')}
                placeholder={`Enter`}
              />
            </div>
          </div>

          <div className="af-details">
            <div className="af-div pad-12r">
              <div className="af-title">OFF</div>
              <InputElement
                className="Add_flight_box"
                value={this.obj.offutcdttm}
                onChange={e => {
                  this.obj.offutcdttm = e.target.value;
                  this.setState({});
                }}
                blur={() => this.checkValid('offutcdttm')}
                placeholder={`Enter`}
              />
            </div>

            <div className="af-div pad-12r">
              <div className="af-title">ATA</div>
              <InputElement
                className="Add_flight_box"
                value={this.obj.actarrutcdttm}
                onChange={e => {
                  this.obj.actarrutcdttm = e.target.value;
                  this.setState({});
                }}
                blur={() => this.checkValid('actarrutcdttm')}
                placeholder={`Enter`}
              />
            </div>
          </div>

          <div className="Cancel_Done">
            <Button
              className="flgt-mngt-cancel-button"
              ButtonHandler={() => {
                this.props.handleChange(false);
              }}
            >
              Cancel
            </Button>
            <Button
              className="flgt-mngt-done-button"
              ButtonHandler={() => {
                console.log(this.obj);

                for (const key in this.obj) {
                  if (this.obj.hasOwnProperty(key)) {
                    const element = this.obj[key];
                    if (!element || element === '') {
                      commonAction.openAlert({
                        title: 'Warning',
                        content: 'All field must be filled',
                      });
                      return;
                    }
                  }
                }
                const response = {
                  ...this.obj,
                  actarrutcdttm: tzFormat(
                    this.obj.actarrutcdttm,
                    this.arrivalDate
                  ),
                  schdeputcdttm: tzFormat(
                    this.obj.schdeputcdttm,
                    this.departureDate
                  ),
                  scharrutcdttm: tzFormat(
                    this.obj.scharrutcdttm,
                    this.arrivalDate
                  ),
                  estdeputcdttm: tzFormat(
                    this.obj.estdeputcdttm,
                    this.departureDate
                  ),
                  estarrutcdttm: tzFormat(
                    this.obj.estarrutcdttm,
                    this.arrivalDate
                  ),
                  actdeputcdttm: tzFormat(
                    this.obj.actdeputcdttm,
                    this.departureDate
                  ),
                  onutcdttm: tzFormat(this.obj.onutcdttm, this.arrivalDate),
                  offutcdttm: tzFormat(this.obj.offutcdttm, this.departureDate),
                };
                this.props.postAddCall(response);

                this.props.handleChange(false);
              }}
            >
              Done
            </Button>
          </div>
        </div>
      </>
    );
  }
}

const mapState = state => {
  return {
    aircraftRegddreducer: state.Aircraftinitialdata,
    stationreducer: state.Stationinitialdata,
    postAddFlight: state.fltMngtUpdate,
    fltstatusDDReducer: state.flightManagementTemplate,
  };
};

const actionCreators = {
  aircraftRegdd: AircraftRegActions.getAircraftRegDetails,
  stationdd: StationActions.getStationDetails,
  postAddCall: flightUpdateActions.postFLT_ADDFlight,
  fltstatusDD: templateActions.getFLT_Template,
};

export default connect(mapState, actionCreators)(AddFlightList);
