import React, { Component } from 'react';
import { FlightSchedules } from './flight-schedule';
import { FlightInformation } from './flight-information';
import { FlightTurnaround } from './flight-turnaround';
import '../../flight-management.scss';
import { flightUpdateActions } from '../../../../../Store/flightManagement/flightUpdate/flightUpdate.action';
import { connect } from 'react-redux';
import { templateActions } from '../../../../../Store/flightManagement/template/template.action';
import { CrewOnFlight } from './crewonflight';
import { crewOnFlightActions } from '../../../../../Store/flightManagement/crewOnFlight/crewOnFlight.action';
import { flightTurnAroundAction } from '../../../../../Store/flightManagement/flightUpdate/flightTurnAround/flightTurnAround.action';

class FlightUpdate extends Component {
  componentDidMount() {
    console.log('hi', this.props.updateDetails);
    this.props.getUpdate();
    this.props.getCrewOnFlight(this.props.updateDetails.id);
    this.props.getflightTurnAround(this.props.updateDetails.turnId);
  }
  fltMgntList = [];
  fltcrew = [];

  state = {
    list: [],
    crew: [],
  };

  updatedData = data => {
    console.log(data,"flightdata")
    this.props.updateHandler({ ...this.props.updateDetails, ...data });
  };

  render() {
    console.log('this.props.flights', this.props.flights);
    return (
      <div className="update-details-container">
        <div className="Extenddiv">
          <FlightSchedules
            flights={this.props.flights}
             inputFields={this.props.updateDetails.FlightSchedule}
            // callback={this.updatedData}
          />
          <FlightInformation
            flights={this.props.flights}
            callback={this.updatedData}
            inputFields={this.props.updateDetails.FlightInformation}
          />
          <FlightTurnaround
            flights={this.props.flights}
            // callback={this.updatedData}
            turnData={this.props.turnAroundData}
             inputFields={this.props.updateDetails.FlightTurnAround}
          />
          <CrewOnFlight crew={this.props.crew} />
        </div>
      </div>
    );
  }
}

function mapState(state) {
  return {
    ...state.fltMngtUpdate,
    ...state.fltMntCrewOnFlightReducer,
    ...state.fltTurnAroundReducer,
  };
}

const actionCreators = {
  getUpdate: flightUpdateActions.getFLT_Update,
  getCrewOnFlight: crewOnFlightActions.getFLT_CREWONFLIGHT,
  postUpdatedData: flightUpdateActions.storeFLT_Update,
  getflightTurnAround: flightTurnAroundAction.getFLT_TurnAround,
};

export default connect(mapState, actionCreators)(FlightUpdate);
