class FileUpload extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedFile: [],

      uploadInfo: [],
      // errorDetails: [],
    };
    // this.props.getUploadDetails();
  }

  componentDidMount() {
    this.props.getTemplate();
    // this.props.getErrorMessage();
    this.props.getUploadDetails();
    // console.log('props', this.props.errorDetails);

    console.log('hsf', this.props);
    // console.log("gok",this.setState1);

    // this.setState({}, () => {
    //   this.props.getUploadDetails();
    // });
    // console.log('helloow', this.setState({}));
  }

  uploadedDateandTime(date) {
    const monthNames = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sep',
      'Oct',
      'Nov',
      'Dec',
    ];
    var day = date.getDate();
    var month = monthNames[date.getMonth()];
    var year = date
      .getFullYear()
      .toString()
      .slice(-2);
    var hour = date.getHours();
    var min = date.getMinutes();
    return `${day} ${month}'${year} ${hour}:${min}`;
  }
  checkMimeType = event => {
    //getting file object
    let files = event.target.files;
    //define message container
    let err = [];
    // list allow mime type
    const types = [
      'xls',
      'xlsx',
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      'application/vnd.ms-excel',
    ];

    // loop access array
    for (var x = 0; x < files.length; x++) {
      // compare file type find doesn't matach
      if (types.every(type => files[x].type !== type)) {
        // create error message and assign to container
        //  err[x] = files[x].type+' is not a supported format\n';
        console.log(err[x]);
      }
    }
    for (var z = 0; z < err.length; z++) {
      // if message not same old that mean has error
      // discard selected file
      // toast.error(err[z])
      event.target.value = null;
    }
    return true;
  };
  maxSelectFile = event => {
    let files = event.target.files;
    if (files.length > 15) {
      const msg = 'Only 15 files can be uploaded at a time';
      event.target.value = null;
      //  toast.warn(msg)
      console.log(msg);
      return false;
    }
    return true;
  };
  checkFileSize = event => {
    let files = event.target.files;
    let size = 2000000;
    let err = [];
    for (var x = 0; x < files.length; x++) {
      if (files[x].size > size) {
        err[x] = files[x].type + 'is too large, please pick a smaller file\n';
      }
    }
    for (var z = 0; z < err.length; z++) {
      // if message not same old that mean has error
      // discard selected file
      //toast.error(err[z])
      event.target.value = null;
    }
    return true;
  };
  onChangeHandler = event => {
    var files = event.target.files;
    if (
      this.maxSelectFile(event) &&
      this.checkMimeType(event) &&
      this.checkFileSize(event)
    ) {
      // if return true allow to setState
      this.setState(
        {
          uploadInfo: true,
        },
        () => {
          // trigger clickhandler

          this.onClickHandler();
        }
      );
    }
  };

  individualFileUpload = (selectedFile, index) => {
    const data = new FormData();
    data.append('file', selectedFile);

    // let info = _storage.getItem('file_upload_info');
    // let { uploadedFiles = [], uploadingFiles = [], count = 0 } = info;
    // let tempCount = count;
    // uploadingFiles.push({
    //   name: selectedFile.name,
    //   uploading: true,
    //   percentage: 10,
    //   count,
    // });
    // count++;
    // info = { ...info, uploadedFiles, uploadingFiles, count };
    // _storage.setItem('file_upload_info', info);
    axios
      .post(
        environment.FlightManagement.fileUpload,
        //  +
        //   '?' +
        //   localStorage.getItem('token'),
        data
      )

      //  {
      //   onUploadProgress: ProgressEvent => {
      // let info = _storage.getItem('file_upload_info');
      // let { uploadingFiles } = info;
      // uploadingFiles = uploadingFiles.map(val => {
      //   if (tempCount === val.count) {
      //     val.percentage =
      //       (ProgressEvent.loaded / ProgressEvent.total) * 100;
      //   }
      //   return val;
      // });
      // _storage.setItem('file_upload_info', { ...info, uploadingFiles });
      //     this.setState({
      //       uploadingFiles,
      //     });
      //   },
      // })
      .then(res => {
        this.setState({}, () => {
          this.props.getUploadDetails();
        });
        console.log('resultt', res);
        // this.props.getErrorMessage();
        console.log('uploadedDetails', this.setState);

        // let info = _storage.getItem('file_upload_info');
        // let { uploadingFiles, uploadedFiles } = info;
        // let newUploadedFile = {};
        // uploadingFiles = uploadingFiles.filter(val => {
        //   if (tempCount === val.count) {
        //     val.uploadedDate = this.uploadedDateandTime(new Date());
        //     newUploadedFile = val;
        //     return false;
        //   }
        //   return val;
        // });
        // uploadedFiles = [...uploadedFiles, newUploadedFile];

        // info = {
        //   ...info,
        //   uploadedFiles,
        //   uploadingFiles,
        // };
        // _storage.setItem('file_upload_info', info);
        this.setState({}, () => {
          this.props.getTemplate();
        });
      })
      .catch(err => {
        console.log(err);
        // let info = _storage.getItem('file_upload_info');
        // let { uploadingFiles } = info;
        // uploadingFiles = uploadingFiles.map(val => {
        //   if (val.count === tempCount) {
        //     val.uploading = false;
        //   }
        //   return val;
        // });
        // info = {
        //   ...info,
        //   uploadingFiles,
        // };
        // _storage.setItem('file_upload_info', info);

        this.setState({});
      });
  };

  onClickHandler = () => {
    // uploading files save to array
    for (var x = 0; x < this.state.selectedFile.length; x++) {
      const file = this.state.selectedFile[x];
      this.individualFileUpload(file, x);
    }
    this.props.postFileUpload();
    this.setState({});
  };

  render() {
    const fileUploadInfo = _storage.getItem('file_upload_info');
    console.log(this.props.uploadDetails, 'upload details');
    return (
      <div className="file-upload">
        <DragAndDrop
          handleDrop={files => {
            this.setState({ selectedFile: files });
            this.onClickHandler();
          }}
        >
          <div className="file-upload-input">
            <p className="upload-file-content">
              {' '}
              <span> Select Files </span> or Drop here to upload{' '}
            </p>
            <InputElement
              type="file"
              onChange={this.onChangeHandler}
              multiple
            />
          </div>
        </DragAndDrop>
        <UploadingStatus uploadingFiles={fileUploadInfo.uploadingFiles || []} />
        <UploadedStatus
          uploadedFiles={this.props.uploadDetails || []}
          errorResponse={this.props.getErrorMessage}
        />
      </div>
    );
  }
}

const UploadingStatus = ({ uploadingFiles }) => {
  if (uploadingFiles.length === 0) return null;
  return (
    <div className="uploading-status-container">
      <div className="uploading-status-head">
        {`Uploading ${uploadingFiles.length} files`}
      </div>
      <div className="uploading-status">
        {uploadingFiles.map(file => {
          return (
            <div className="uploading-file">
              <div className="file-name">{file.name}</div>
              <div className="file-status">
                {file.uploading ? (
                  <span>
                    <CircularProgressbar
                      value={100}
                      strokeWidth={5}
                      styles={buildStyles({
                        strokeLinecap: 'butt',
                        pathColor: 'green',
                        backgroundColor: '#fff',
                        trailColor: 'transparent',
                      })}
                    />
                    <CircularProgressbar
                      value={file.percentage}
                      strokeWidth={50}
                      styles={buildStyles({
                        strokeLinecap: 'butt',
                        pathColor: 'green',
                        backgroundColor: '#fff',
                        trailColor: 'transparent',
                      })}
                    />
                  </span>
                ) : (
                    <Failed />
                  )}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

const UploadedStatus = ({ uploadedFiles, errorResponse }) => {
  useEffect(() => {
    setFiles(uploadedFiles);
  }, [uploadedFiles.length]);

  const date = value => {
    let val = value !== undefined ? value : '';
    var now = new Date(val);
    console.log(now, 'date');
    var dateString = moment(now).format('DD MMM YY');
    return dateString.toUpperCase();
  };

  const time = value => {
    let val = value !== undefined ? value : '';
    var str = val.slice(0, -1);
    var now = new Date(str);
    var timestring = moment(now).format('HH:mm');
    return timestring;
  };

  const filename = name => {
    let tempfilename = name !== undefined ? name : '';
    if (tempfilename.length >= 20) {
      let filename = tempfilename.slice(8);
      // let extfilename = ext.concat(filename);
      return `... ${filename}`;
    } else {
      return name;
    }
  };

  const [files, setFiles] = useState(uploadedFiles);
  if (uploadedFiles.length === 0) return null;
  const searchHandler = (e, list) => setFiles(list);

  return (
    <div className="uploaded-status-container">
      <div className="uploaded-status-head">
        <div> {`Uploaded ${uploadedFiles.length} files`}</div>
        <div>
          <SearchBox
            className="file-upload-search-bar"
            placeholder="Search by name"
            list={uploadedFiles}
            onChange={searchHandler}
            filterBy={['fileName']}
          />
        </div>
      </div>
      <div className="uploaded-status">
        {files.map(file => {
          return (
            <div
              className="uploaded-file"
              onClick={() => {
                errorResponse(file.fileName, file.dateOfUpload);
              }}
            >
              <div className="file-name">{filename(file.fileName)}</div>
              <div className="file-time">
                <span style={{ marginRight: '10px' }}>
                  {date(file.dateOfUpload)}
                </span>
                {time(file.dateOfUpload)}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

// export default FileUpload;

function mapState(state) {
  console.log(state.fileuploadReducer, 'statefrom flight error');
  return state.flightManagementTemplate, state.fileuploadReducer;
}

const actionCreators = {
  getTemplate: templateActions.getFLT_Template,
  getErrorMessage: fileUploadAction.getFLT__ERROR_MESSAGE,
  getUploadDetails: fileUploadAction.getFLT__UPLOAD_INFO,
  postFileUpload: fileUploadAction.postFLT_UPLOAD_INFO,
};

export default connect(mapState, actionCreators)(FileUpload);

