import React from 'react';
import './flight-management.scss';
import SearchBox from '../../Atoms/SearchBox';
import Accordion from '../../Atoms/Accordion';
import FileUpload from './presentation/file-upload';
import { templateActions } from '../../../Store/flightManagement/template/template.action';
import { connect } from 'react-redux';
import { FlightList } from './presentation/flight-list';
import FlightUpdate from './presentation/flightUpdate/index';
import Slide from '../../Atoms/slidedown';
import AddTask from '../../Molecules/AddTask';
import AddFlightList from './addFlightList/add-flight';
import filter from '../../../assets/Icon/Group 208.svg';
import Button from '../../Atoms/Button';
import Popover, { ArrowContainer } from 'react-tiny-popover';
import DatePicker from '../../Atoms/Datepicker/index';

class FlightMangement extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.togglePopover = this.togglePopover.bind(this);
    this.handleClose = this.handleClose.bind(this);

    // localStorage.setItem(
    //   'token',
    //   'token=eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..I9cDxbHjipKRXHuhgkJhSeTvsyCi1_ebSyV3KH-7gYE'
    // );
  }
  togglePopover = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };

  handleChange(isSubmitted) {
    this.setState({ isFlt_Mgnt_addFlight: isSubmitted });
    this.setState({ searchBoxValue: '' });
  }

  handleClose() {
    this.setState({ isOpen: false });
  }

  updateData = (data, index) => {
    const { updateDetails } = this.props;
    updateDetails[index] = data;
    this.props.updateDetailsData(updateDetails);
  };
  state = {
    searchBoxValue: '',
    isFlt_Mgnt_addFlight: false,
    list: [],
    isOpen: false,
  };
  rerender = false;
  componentDidMount() {
    this.props.getTemplate();
  }

  searchHandler = (e, list) => {
    this.setState({ searchBoxValue: e.target.value, list });
  };
  generateList = (templateList, updateDetails) => {
    //check the flight list must be array
    if (!Array.isArray(templateList)) {
      return null;
    }

    let list = [...templateList];
    //check whether searchbox have value
    if (this.state.searchBoxValue.length) {
      list = this.state.list;
    }

    // generate list
    return list.map((data, index) => {
      return (
        <Slide
          key={index}
          header={
            <FlightList
              key={index}
              flights={data}
              updateData={updateDetails[index]}
            />
          }
          content={
            <FlightUpdate
              flights={data}
              updateDetails={updateDetails[index]}
              updateHandler={details => {
                this.updateData(details, index);
              }}
            />
          }
        />
      );
    });
  };

  render() {
    const { templateData, updateDetails } = this.props;
    this.fltMgntList = this.generateList(templateData, updateDetails);

    return (
      <div className="main flight-management">
        <Header
          searchHandler={this.searchHandler}
          handleChange={this.handleChange}
          isAddFlight={this.state.isFlt_Mgnt_addFlight}
          list={templateData}
          searchBoxValue={this.state.searchBoxValue}
          togglePopover={this.togglePopover}
          isOpen={this.state.isOpen}
          handleClose={this.handleClose}
        />

        <div className="flight-manangement-area">
          {this.state.isFlt_Mgnt_addFlight ? (
            <AddFlightList handleChange={this.handleChange} />
          ) : (
            <div className="flight-management-list">{this.fltMgntList}</div>
          )}
          {/* <div className="accordian-area">
            <Accordion menuList={Menu} />
          </div> */}
        </div>
      </div>
    );
  }
}

const FlightSchedule = () => {
  return <FileUpload />;
};

