export const Menu = [
  {
    title: 'File Upload',
    data: FlightSchedule,
  },
  // {
  //   title: 'Crew On Flight',
  //   data: FlightSchedule,
  // },
  // {
  //   title: "Optional place holder",
  //   data: FlightSchedule
  // }
];

function mapState(state) {
  console.log(state,"flight template")
  return state.flightManagementTemplate;
}

const actionCreators = {
  getTemplate: templateActions.getFLT_Template,
  updateDetailsData: templateActions.updateDetails,
};

export default connect(mapState, actionCreators)(FlightMangement);
