import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import '../Datepicker/date.scss';
import arrow from '../../Img/arrow-icon.png';
import moment from 'moment';

export default function Date_picker(props) {
  const [startDate, setStartDate] = useState(new Date());

  // const years = [2015, 2016, 2017, 2018, 2019, 2015, 2016, 2017, 2018, 2019];
  // if (years.length - 1 !== dateString) {
  //   console.log(years.length - 1, dateString, 'years');
  //   years.push(dateString);
  // }
  var dateString = moment().format('YYYY');
  let years = [];
  let futureyear = moment()
    .add(1, 'years')
    .format('YYYY');
  years.push(dateString, futureyear);
  const months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];
  return (
    <div>
      <DatePicker
        renderCustomHeader={({
          date,
          changeYear,
          decreaseMonth,
          increaseMonth,
          prevMonthButtonDisabled,
          nextMonthButtonDisabled,
        }) => (
          <div>
            <div className="YearBlock">
              <div className="yearul">
                {years.map(year => (
                  <div onClick={() => changeYear(year)} className="yearli">
                    {year}
                  </div>
                ))}
              </div>
            </div>
            <div className="header_Calender">
              <img
                className="leftarrow"
                onClick={decreaseMonth}
                disabled={prevMonthButtonDisabled}
                src={arrow}
              />
              <div className="monthname">{months[date.getMonth()]}</div>
              <img
                className="rightarrow"
                onClick={increaseMonth}
                disabled={prevMonthButtonDisabled}
                src={arrow}
              />
            </div>
          </div>
        )}
        selected={startDate}
        onChange={date => {
          setStartDate(date);
          props.Getbackdate(date);
        }}
        showYearDropdown
        dropdownMode="select"
      />
    </div>
  );
}
