import React, { Component } from "react";
import "../../flight-management.scss";
import str from "../../../../Img/str.png";
import clove from "../../../../Img/clove.png";
import date from "../../../../Img/date.png";
import fly from "../../../../Img/fly.png";
import { Allimages } from "../../../../../Utils/imageRetriver";
import moment from 'moment';
export const CrewOnFlight = props => {
  console.log("crew", props);

  const crews = props.crew || [];
  
    const DateMonthConverter = dateString => {
      const dateMonth = new Date(dateString).toDateString().split(" ");
      console.log(dateString);
      const month = dateMonth[1] || "";
      const date = dateMonth[2] || "";
      const year =dateMonth[3] || "";
      const str = year.slice(2);
      return `${date} ${month.toUpperCase()} ${str}`;
      console.log(`${date} ${month} ${str}`);
    };

    const timeConverter = timeString => {
      //  const time = new Date(timeString).toLocaleTimeString().split(":");
    //   const time = new Date(timeString);
    //  const finalhrs=time.toLocaleTimeString(time.setHours(time.getHours()+ 6)).split(':');
    //   const finalmin=time.toLocaleTimeString(time.setMinutes(time.getMinutes()+ 30)).split(':');
    // const time = new Date(timeString).getUTCFullYear();
    // const hour=time.getUTCHours().toString();
    // const min=time.getUTCMinutes().toString();
      const time = new Date(timeString).toTimeString().split(':');
            const hour = time[0] || "0";
      const min = time[1] || "0";
      return `${hour.length == 1 ? "0" + hour : hour}:${
        min.length == 1 ? "0" + min : min
      }`;

    }
   
  const  time=(value)=>{
          let val = value!== undefined ? value : ""
          var str = val.slice(0, -1);
          var now = new Date(str);
          var timestring = moment(now).format('HH:mm');
          return timestring;
       }
  return (
   
    <>
    
      <div className="ScheduleHeader">
        <div className="ScheduleHeading">Crew On Flight</div>
        {/* <div className="flight-list"> */}
        <div className="crew-list">
          {crews.map((value, key) => (
            <div className="crew-list" key={key}>
              <div className="crew-info">
                {/* <div className="Crew"> */}
                <div className="CrewDetails1 ">
                  <div className="CrewDetailsHeader">{value.staffid}</div>
                </div>

                <div className="CrewDetails2">
                  <div className="CrewDetailsName">
                    <b>
                      {value.crewfirstname} {value.crewlastname}
                    </b>
                  </div>
                  {/* <div className="Rectangle"><div className="RectangleFonts"><center>#</center>   </div></div>
                  <div className="Rectangle"><div className="RectangleFonts"><center>></center>   </div></div> */}
                </div>

                <div className="CrewDetails3">
                  <div className="CrewDetailsName">{value.crewbasestation}</div>
                </div>

                <div className="CrewDetails4">
                  <div className="CrewDetailsHeader">
                    <img src={str} /> {value.actingrank}
                  </div>
                  {/* <img src="../../../../Img/star.jpg"></img>FSS </div> */}
                </div>

                <div className="CrewDetails5">
                  <div className="CrewDetailsHeader">
                    {/* <img src={date} /> */}
                     {DateMonthConverter(value.reportingdttm)}  
                  </div>

                </div>


                <div className="CrewDetails6">
                <div className="CrewDetailsHeader">{timeConverter(value.reportingdttm)}</div>
                    </div>
                <div className="CrewDetails7">
                  <div className="CrewDetailsHeader">{value.contactnumber}</div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};
