import React, { Component, useState, useEffect } from 'react';
import InputElement from '../../../../Atoms/InputElement';
import '../../flight-management.scss';
import { commonAction } from '../../../../../Store/commonActivity/action';
import { timeValidator, numberValidator } from '../../calculation';

export const FlightInformation = props => {
  // const {
  //   schdeplcldttm = '',
  //   scharrlcldttm = '',
  //   offlcldttm = '',
  //   onlcldttm = '',
  //   actarrlcldttm = '',
  //   actdeplcldttm = '',
  //   estarrlcldttm = '',
  //   estdeplcldttm = '',
  //   aircraftregistrationno
  // } = props.flights;
  console.log(props, 'flight information');
  const {
    airlineaircrafttype = '',
    aircraftregistrationno = '',
    depgateno = '',
    baynumber = '',
    paxcount = '',
    ssrcount = '',
    baggagecount = '',
  } = props.flights;

  const setTimeWarning = value =>
    commonAction.openAlert({
      title: 'Warning',
      content: value,
    });

  const inputFields = props.inputFields || {};
  console.log(inputFields,"fieldsinput")
  const updateData = value => {
    console.log(value,"flightvalue")
    props.callback({ FlightInformation: { ...inputFields, ...value } });
  };
  return (
    <>
      <div className="ScheduleHeader">
        <div className="ScheduleHeading">Flight Information</div>

        <div className="flight-list">
          <div className="flight-info">
            <div className="ScheduleDetails1 ">
              <div className="DetailsHeader">AC TYPE</div>
              <div className="Details1 font-weight-bold">
                {airlineaircrafttype}
              </div>
            </div>

            <div className="ScheduleDetails2 ">
              <div className="DetailsHeader">AC REG</div>
              <div className="Details Detailsbox font-weight-bold">
                <div
                  className="flt-mgnt-update-input"
                  value={inputFields.AC_REG}
                  // defaultValue={aircraftregistrationno}
                  // onChange={e => {
                  //   updateData({ AC_REG: e.target.value });
                  // }}
                  // blur={e => {
                  //   if (!inputFields.AC_REG.length) {
                  //     // updateData({ AC_REG: '' });
                  //     setTimeWarning('should be correct format');
                  //   }
                  // }}
                >
                  {aircraftregistrationno}
                </div>
              </div>
            </div>

            <div className="ScheduleDetails3 ">
              <div className="DetailsHeader">Gate</div>
              <div className="Details Detailsbox font-weight-bold">
                {' '}
                <InputElement
                  className="flt-mgnt-update-input"
                  value={inputFields.Gate}
                  defaultValue={depgateno}
                  onChange={e => {
                    updateData({ Gate: e.target.value });
                  }}
                  blur={e => {
                    if (inputFields && inputFields.gate && !inputFields.Gate.length) {
                       updateData({ Gate: '' });
                      setTimeWarning('should be correct format');
                    }
                  }}
                />
              </div>
            </div>

            <div className="ScheduleDetails3 ">
              <div className="DetailsHeader">Bay</div>
              <div className="Details Detailsbox font-weight-bold">
                {' '}
                <InputElement
                  className="flt-mgnt-update-input"
                   value={inputFields.Bay}
                  defaultValue={baynumber}
                  onChange={e => {
                    updateData({ Bay: e.target.value });
                  }}
                  blur={e => {
                    if (inputFields.Bay && !inputFields.Bay.length) {
                       updateData({ Gate: '' });
                      setTimeWarning('should be correct format');
                    }
                  }}
                />
              </div>
            </div>

            <div className="ScheduleDetails4 ">
              <div className="DetailsHeader">Pax Load</div>
              <div className="Details Detailsbox font-weight-bold">
                <InputElement
                  className="flt-mgnt-update-input"
                   value={inputFields.Pax_load}
                  defaultValue={paxcount}
                  onChange={e => {
                    updateData({ Pax_load: e.target.value });
                  }}
                  blur={e => {
                    if (!numberValidator(inputFields.Pax_load)) {
                       updateData({ Pax_load: '' });
                      setTimeWarning('Should be number');
                    }
                  }}
                />
              </div>
            </div>
            <div className="ScheduleDetails7">
              <div className="DetailsHeader">SSR</div>
              <div className="Details Detailsbox font-weight-bold">
                <InputElement
                  className="flt-mgnt-update-input"
                  value={inputFields.PRM}
                  defaultValue={ssrcount}
                  onChange={e => {
                    updateData({ PRM: e.target.value });
                  }}
                  blur={e => {
                    if (!numberValidator(inputFields.PRM)) {
                       updateData({ PRM: '' });
                      setTimeWarning('Should be number');
                    }
                  }}
                />
              </div>
            </div>

            <div className="ScheduleDetails8 ">
              <div className="DetailsHeader">Baggage</div>
              <div className="Details Detailsbox font-weight-bold">
                {' '}
                <InputElement
                  className="flt-mgnt-update-input"
                   value={inputFields.Baggage}
                  defaultValue={baggagecount}
                  onChange={e => {
                    updateData({ Baggage: e.target.value });
                  }}
                  blur={e => {
                    if (!numberValidator(inputFields.Baggage)) {
                       updateData({ Baggage: '' });
                      setTimeWarning('Should be number');
                    }
                  }}
                />
              </div>
            </div>
            <div className="ScheduleDetails9 "></div>

            {/* <Button className='flgt-mngt-update-button' ButtonHandler={()=>this.props.FlightSchedule(false)}>Update</Button>  */}
          </div>
        </div>
      </div>

      {/* </div> */}
      {/* </div> */}
      {/* </div> */}
    </>
  );
};
