import moment from 'moment';

//expected date
// output format date and month **  31 12  **
export const DateMonthConverter = dateString => {
  const dateMonth = new Date(dateString).toDateString().split(' ');
  const month = dateMonth[1] || '';
  const date = dateMonth[2] || '';
  return `${date} ${month}`;
};
export const DateMonthandTimeConverter = (dateString, timeString) => {
  const day = new Date(dateString).getDay();
  const month = new Date(dateString).getMonth();
  const year = new Date(dateString)
    .getFullYear()
    .toString()
    .slice(-2);
  const time = new Date(timeString).toLocaleTimeString();
  const hour = time[0] || '0';
  const min = time[1] || '0';
  console.log(`${day} ${month}'${year} ${hour}:${min}`);

  return `${day} ${month}'${year} ${hour}:${min}`;
};

