// expected format 12:12
//output true or false
export const timeValidator = value => {
  var regexp = /^\d{2}:\d{2}$/gm;
  if (regexp.test(value)) {
    return true;
  }
  return false;
};

export const numberValidator = value => /^\d{0,}$/gm.test(value);

export const tzFormat = (value, date) => {
  const [hour, min] = value.split(':');
  var d = new Date();
  if (date) {
    d = date;
  }
  d.setHours(hour, min, 0);
  return d.toISOString().split('.')[0] + 'Z';
};
