const ActualTimeRenderer = ({
  stationname = '',
  stdorsta = '',
  OutOrOn = '',
  InOrOff = '',
  OnOrOff = '',
  dateMonth = '',
  atdorata = '',
  heading,
}) => {
  return (
    <>
      <div className="journey-details departure-place">
        <span>{stationname} </span> {dateMonth}
      </div>
      <div className="departure-head actual-time">
        <div className="actual-time-place flex-1">
          <div className="time-heading mr-b-2"> {heading[0]} </div>
          <div className="time-value">
            <span className="font-weight-bold">{stdorsta} </span>
          </div>
        </div>
        <div className="actual-time-place flex-1">
          <div className="time-heading mr-b-2 flex-1"> {heading[1]} </div>
          <div className="time-value">
            <span className="font-weight-bold">{atdorata}</span>
          </div>
        </div>
        <div className="actual-time-place flex-1">
          <div className="time-heading mr-b-2 flex-1"> {heading[2]} </div>
          <div className="time-value">
            <span className="font-weight-bold">{OnOrOff}</span>
          </div>
        </div>
      </div>
    </>
  );
};
function mapState(state) {
  return { ...state.fltMngtUpdate };
}

const actionCreators = {
  // getUpdate: flightUpdateActions.getFLT_Update,
  // getCrewOnFlight: crewOnFlightActions.getFLT_CREWONFLIGHT,
  postUpdatedData: flightUpdateActions.storeFLT_Update,
  postfunctionUpdate: flightUpdateActions.storePostFLT_Update,
  updateCall: flightUpdateActions.postFLT_Update,
};

export default connect(mapState, actionCreators)(FlightList);
